import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { API } from '../../config';
import DaumPostCode from './DaumPostCode/DaumPostCode';
import Input from '../../components/Input/Input';
import Button from '../../components/Button/Button';
import ImageFile from '../../components/ImageFile/ImageFile';
import styled from 'styled-components';

/**
 *
 * @property {function} handleSellerInfo -   state  .
 * @property {function} handleAddressSelect -  API    data state  .
 * @property {function} postSellerInfoSubmitBtn -     .
 */

const SellerSignUp = props => {
  const [sellerInfo, setSellerInfo] = useState({
    sellerName: '',
    zipCode: '',
    address: '',
    addressDetail: '',
    phoneNumber: '',
  });
  const [onAddressSelect, setOnAddressSelect] = useState(''); //  API    data state 
  const [upLoadedImage, setUpLoadedImage] = useState(null); //  url state

  // var
  const navigate = useNavigate();
  const { sellerName, zipCode, address, addressDetail, phoneNumber } =
    sellerInfo; //   

  // function
  const handleSellerInfo = e => {
    const { name, value } = e.target;
    setSellerInfo({ ...sellerInfo, [name]: value });
  };

  const handleAddressSelect = address => {
    setOnAddressSelect(address);
    setSellerInfo({
      ...sellerInfo,
      zipCode: address.zonecode,
      address: address.address,
    });
  };

  const postSellerInfoSubmitBtn = e => {
    e.preventDefault();

    const formData = new FormData(); // formData 
    formData.append('image', upLoadedImage); // formData image 

    const seller = {
      name: sellerName,
      zipCode,
      address,
      addressDetails: addressDetail,
      phoneNumber,
    };
    formData.append('seller', JSON.stringify(seller)); // formData seller (JSON  )
    // fetch('http://10.58.52.64:8000/users/seller', {
    fetch(`${API.USERS}/seller`, {
      method: 'POST',
      headers: {
        // 'Content-Type': 'multipart/form-data', // formData   Content-Type  . form  enctype    multipart/form-data .
        Authorization: localStorage.getItem('accessToken'),
      },
      body: formData,
    })
      .then(response => response.json())
      .then(result => {
        if (result.message === 'SUCCESS') {
          alert('  .');
          localStorage.setItem('isSeller', true);
          navigate('/main');
          window.location.reload();
        } else {
          alert('  .');
        }
      });
  };

  const isDisabledBtnValid =
    sellerName.length >= 2 && phoneNumber.length === 11;

  return (
    <Main id="main">
      <div>
        <SignUpContainer>
          <SellerSignUpLeftSection>
            <span> </span>
          </SellerSignUpLeftSection>
          <SellerSignUpRightSection>
            <SellerSignUpForm
              onSubmit={postSellerInfoSubmitBtn}
              onChange={handleSellerInfo}
              encType="multipart/form-data"
            >
              <fieldset>
                <SellerSignUpLegend> </SellerSignUpLegend>
              </fieldset>
              <SellerSignUpInputWrap>
                <Input
                  type="text"
                  placeholder="  ."
                  borderRadius="4px"
                  name="sellerName"
                  status={
                    (sellerInfo?.sellerName?.length === 0 && 'default') ||
                    (sellerInfo?.sellerName?.length < 2 && 'error')
                  }
                  error="    ."
                />
              </SellerSignUpInputWrap>
              <SellerSignUpInputWrap>
                <ImageFile
                  name="image"
                  borderRadius="4px"
                  placeholder=""
                  upLoadedImage={upLoadedImage}
                  setUpLoadedImage={setUpLoadedImage}
                />
              </SellerSignUpInputWrap>

              <SellerSignUpAddressWrap>
                <Input
                  type="text"
                  placeholder=""
                  value={zipCode}
                  name="zipCode"
                  labelFlex="1"
                  borderRadius="4px"
                />
                <DaumPostCode onAddressSelect={handleAddressSelect} />
              </SellerSignUpAddressWrap>
              <SellerSignUpInputWrap>
                <Input
                  type="text"
                  placeholder=""
                  value={address}
                  name="address"
                  labelFlex="1"
                  borderRadius="4px"
                />
              </SellerSignUpInputWrap>
              <SellerSignUpInputWrap>
                <Input
                  type="text"
                  placeholder=""
                  name="addressDetail"
                  labelFlex="1"
                  borderRadius="4px"
                />
              </SellerSignUpInputWrap>
              <SellerSignUpInputWrap>
                <Input
                  type="text"
                  placeholder=" "
                  name="phoneNumber"
                  labelFlex="1"
                  borderRadius="4px"
                />
              </SellerSignUpInputWrap>
              <SellerSignUpButtonWrap>
                <Button
                  type="submit"
                  content=""
                  shape="solid"
                  color="primary"
                  onClick={postSellerInfoSubmitBtn}
                  disabled={!isDisabledBtnValid}
                />
              </SellerSignUpButtonWrap>
            </SellerSignUpForm>
          </SellerSignUpRightSection>
        </SignUpContainer>
      </div>
    </Main>
  );
};

export default SellerSignUp;

const Main = styled.main`
  display: flex;
  align-items: center;
`;

const SignUpContainer = styled.div`
  display: flex;
  justify-content: center;
`;

const SellerSignUpLeftSection = styled.section`
  flex: 1;
  border-radius: 4px;
  background: url(/images/account/bg_login.jpg) no-repeat center/cover;
  font-size: 0;
`;

const SellerSignUpRightSection = styled.section`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1;
`;

const SellerSignUpForm = styled.form`
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 80px 20%;
`;

const SellerSignUpLegend = styled.legend`
  font-size: 30px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 20px;
`;

const SellerSignUpInputWrap = styled.div`
  width: 100%;
  margin-top: 20px;
  color: ${props => props.theme.grayscaleF};
`;

const SellerSignUpAddressWrap = styled.div`
  display: flex;
  margin-top: 20px;
  gap: 8px;

  button {
    width: 150px;
  }
`;

const SellerSignUpButtonWrap = styled.div`
  width: 100%;
  margin-top: 20px;
  color: ${props => props.theme.grayscaleF};
`;
