import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { API } from '../../../config';
import { useDispatch } from 'react-redux';
import Counter from '../../Counter/Counter';
import Button from '../../Button/Button';
import styled, { css } from 'styled-components';

/**
 * Modal/Contents/Purchase.js logics
 * @property {function} cartProcess     -   . postOrder() putInCart()   .
 * @property {function} postOrder       -  (, , ,  )   .
 * @property {function} putInCart       -  (productId, quantity) Redux store     .
 * @property {function} buyNowProcess   -   .        .
 */

const Purchase = ({ productId, productName, totalPrice, onClose }) => {
  const [quantity, setQuantity] = useState(1);
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const productData = {
    productId: productId,
    quantity: quantity,
  };

  const finalPrice = totalPrice * quantity;

  const cartProcess = () => {
    postCart();
    putInCart();
  };

  const postCart = () => {
    // fetch('http://10.58.52.149:8000/order', {
    // fetch('http://10.58.52.140:8000/carts', {
    fetch(`${API.CART}`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        authorization: localStorage.getItem('accessToken'),
      },
      body: JSON.stringify(productData),
    })
      .then(response => response.json())
      .then(result => {
        console.log(result);
        // setCartData(result.data);
      });
  };

  const putInCart = () => {
    // [Redux]        . store   dispatch   ,    .  payload   
    dispatch({
      type: 'ADD',
      payload: { productId: productId, quantity: quantity },
    });
    onClose();
  };

  const buyNowProcess = () => {
    navigate('/order', {
      state: {
        productData,
        course: 'directly',
        finalPrice: finalPrice,
      },
    });
  };

  return (
    <CartModalWrap>
      <CartModalInnerWrap>
        <ProductName>{productName}</ProductName>
        <PriceDisplay>
          <Counter quantity={quantity} setQuantity={setQuantity} />
          <ProductPrice>{finalPrice?.toLocaleString()}</ProductPrice>
        </PriceDisplay>
      </CartModalInnerWrap>
      <ButtonGroup>
        <Button
          shape="solid"
          color="neutral"
          size="medium"
          content=""
          onClick={cartProcess}
        />
        <Button
          shape="solid"
          color="primary"
          size="medium"
          content=""
          onClick={buyNowProcess}
        />
      </ButtonGroup>
    </CartModalWrap>
  );
};

const FlexCenter = css`
  display: flex;
  justify-content: center;
  align-items: center;
`;

const CartModalWrap = styled.div`
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 20px;
`;

const CartModalInnerWrap = styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  width: 100%;
  height: 100%;
  gap: 40px;
`;

const ProductName = styled.strong`
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  word-wrap: break-word;
  white-space: normal;
  align-self: center;
  font-size: 32px;
  line-height: 1.2;
`;

const PriceDisplay = styled.div`
  ${FlexCenter};
  width: 100%;

  & > div {
    flex: 1;
    height: 100%;
  }
`;

const ProductPrice = styled.span`
  flex: 2;
  display: flex;
  justify-content: flex-end;
  font-size: 32px;
`;

const ButtonGroup = styled.div`
  display: flex;
  gap: 16px;
  width: 100%;

  & > button {
    flex: 1;
  }
`;

export default Purchase;
