import React, { useState } from 'react';
import { useNavigate } from 'react-router';
import { API } from '../../config';
import Input from '../../components/Input/Input';
import Button from '../../components/Button/Button';
import DaumPostCode from './DaumPostCode/DaumPostCode';
import styled from 'styled-components';

/**
 * SingUp.js logics
 * @property {function} handleUserInfo =   state  
 * @property {function} handleAddressSelect =  API    data state  
 * @property {function} postSignUp =  POST API  
 * @property {function} handleSubmitUserInfo =     
 * @property {function} postDuplicateCheck =   API  
 */

const SignUp = props => {
  // userInfo state  ( )
  const [userInfo, setUserInfo] = useState({
    email: '',
    password: '',
    passwordCheck: '',
    name: '',
    phone: '',
    zipCode: '',
    address: '',
    addressDetail: '',
  });

  const [onAddressSelect, setOnAddressSelect] = useState('');
  const navigate = useNavigate();

  const handleUserInfo = e => {
    const { name, value } = e.target; // e.target  name  value .
    setUserInfo({ ...userInfo, [name]: value }); // userInfo   name   value  .
  };

  const emailRegExp =
    /^[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*\.[a-zA-Z]{2,3}$/i; //  
  const passwordRegExp = /^(?=.*[a-zA-Z])(?=.*[!@#$%^*+=-])(?=.*[0-9]).{8,20}$/; //   

  const isEmailValid = emailRegExp.test(userInfo.email); //   
  const isPasswordValid = passwordRegExp.test(userInfo.password); //   
  const isPasswordCheckValid = userInfo.password === userInfo.passwordCheck; //   

  const isValidCheck =
    isEmailValid &&
    isPasswordValid &&
    isPasswordCheckValid &&
    userInfo.name.length >= 2 &&
    userInfo.phone.length === 11; //     

  const handleAddressSelect = address => {
    setOnAddressSelect(address); //  API    data state 
    setUserInfo({
      // zipCode, address     useState 
      ...userInfo,
      zipCode: address.zonecode,
      address: address.address,
    });
  };

  const postSignUp = () => {
    fetch('/data/signup.json', {
      // fetch(`${API.USERS}/signup`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json;charset=utf-8',
      },
      body: JSON.stringify({
        email: userInfo.email,
        password: userInfo.password,
        name: userInfo.name,
        phoneNumber: userInfo.phone,
        zipCode: userInfo.zipCode,
        address: userInfo.address,
        addressDetails: userInfo.addressDetail,
      }),
    })
      .then(response => response.json())
      .then(result => {
        if (result.message === 'SUCCESS') {
          navigate('/login');
        } else {
          alert(' .');
        }
      });
  };

  const handleSubmitUserInfo = e => {
    e.preventDefault(); // submit   
    postSignUp();
  };

  const postDuplicateCheck = () => {
    //   API 
    fetch('/data/duplicate.json', {
      // fetch(`${API.USERS}/duplicate`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json;charset=utf-8',
      },
      body: JSON.stringify({
        email: userInfo.email,
      }),
    })
      .then(response => response.json())
      .then(result => {
        if (result.message === 'SUCCESS') {
          alert('  .');
        } else {
          alert('  .');
        }
      });
  };

  return (
    <Main id="main">
      <div>
        <SignUpContainer>
          <SignUpLeftSection>
            <span></span>
          </SignUpLeftSection>
          <SignUpRightSection>
            <SignUpForm
              onChange={handleUserInfo}
              onSubmit={handleSubmitUserInfo}
            >
              <fieldset>
                <SignUpLegend></SignUpLegend>

                <SignUpEmailWrap>
                  <Input
                    placeholder=" ."
                    borderRadius="4px"
                    name="email"
                    labelFlex="1"
                    status={
                      (userInfo.email.length === 0 && 'default') ||
                      (!isEmailValid &&
                        userInfo.email.length >= 1 &&
                        'error') ||
                      (isEmailValid && 'done')
                    }
                    error="   ."
                    done="  ."
                  />
                  <Button
                    content=""
                    shape="solid"
                    color="primary"
                    onClick={postDuplicateCheck}
                  />
                </SignUpEmailWrap>
                <SignUpInputWrap>
                  <Input
                    type="password"
                    placeholder=" ."
                    borderRadius="4px"
                    name="password"
                    status={
                      (userInfo.password.length === 0 && 'default') ||
                      (!isPasswordValid &&
                        userInfo.password.length >= 1 &&
                        'error') ||
                      (isPasswordValid && 'done')
                    }
                    error=" 8~20  , ,  ."
                    done="  ."
                  />
                </SignUpInputWrap>
                <SignUpInputWrap>
                  <Input
                    type="password"
                    placeholder="   ."
                    borderRadius="4px"
                    name="passwordCheck"
                    status={
                      (userInfo.passwordCheck.length === 0 && 'default') ||
                      (!isPasswordCheckValid &&
                        userInfo.passwordCheck.length >= 1 &&
                        'error') ||
                      (isPasswordCheckValid && 'done')
                    }
                    error="  ."
                    done=" ."
                  />
                </SignUpInputWrap>
                <SignUpInputWrap>
                  <Input
                    placeholder=" ."
                    borderRadius="4px"
                    name="name"
                    status={
                      (userInfo.name.length === 0 && 'default') ||
                      (userInfo.name.length < 2 && 'error')
                    }
                    error="    ."
                  />
                </SignUpInputWrap>
                <SignUpInputWrap>
                  <SignUpAddressWrap>
                    <Input
                      borderRadius="4px"
                      value={userInfo.zipCode}
                      labelFlex="1"
                    />
                    <DaumPostCode onAddressSelect={handleAddressSelect} />
                  </SignUpAddressWrap>
                </SignUpInputWrap>
                <SignUpInputWrap>
                  <Input borderRadius="4px" value={userInfo.address} />
                </SignUpInputWrap>
                <SignUpInputWrap>
                  <Input
                    placeholder=" ."
                    borderRadius="4px"
                    name="addressDetail"
                  />
                </SignUpInputWrap>
                <SignUpInputWrap>
                  <Input
                    type="number"
                    placeholder=" ."
                    borderRadius="4px"
                    name="phone"
                  />
                </SignUpInputWrap>
                <SignUpButtonWrap>
                  <Button
                    type="submit"
                    disabled={!isValidCheck}
                    content=""
                    shape="solid"
                    color="primary"
                    onClick={handleSubmitUserInfo}
                  />
                </SignUpButtonWrap>
              </fieldset>
            </SignUpForm>
          </SignUpRightSection>
        </SignUpContainer>
      </div>
    </Main>
  );
};

export default SignUp;

const Main = styled.main`
  display: flex;
  align-items: center;
`;

const SignUpContainer = styled.div`
  display: flex;
  justify-content: center;
`;

const SignUpLeftSection = styled.section`
  flex: 1;
  border-radius: 4px;
  background: url(/images/account/bg_signup.jpg) no-repeat center/cover;
  font-size: 0;
`;

const SignUpRightSection = styled.section`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1;
`;

const SignUpForm = styled.form`
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 80px 20%;
`;

const SignUpLegend = styled.legend`
  font-size: 30px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 20px;
`;

const SignUpInputWrap = styled.div`
  padding: 15px 0;

  button {
    width: 150px;
  }

  input[type='number']::-webkit-outer-spin-button,
  input[type='number']::-webkit-inner-spin-button {
    -webkit-appearance: none;
    appearance: none;
    margin: 0;
  }
`;

const SignUpButtonWrap = styled.div`
  width: 100%;
  margin-top: 20px;
  color: ${props => props.theme.grayscaleF};
`;

const SignUpAddressWrap = styled.div`
  display: flex;

  gap: 8px;
`;

const SignUpEmailWrap = styled.div`
  display: flex;
  padding: 15px 0;
  gap: 8px;

  button {
    width: 100px;
  }
`;
