import React, { useState } from 'react';
import { API } from '../../config';
import styled from 'styled-components';
import Input from '../../components/Input/Input';
import Button from '../../components/Button/Button';
import DaumPostCode from './DaumPostCode/DaumPostCode';
import { useNavigate } from 'react-router';

const SnsSignUp = props => {
  // userInfo state  ( )
  const [userInfo, setUserInfo] = useState({
    phone: '',
    zipCode: '',
    address: '',
    addressDetail: '',
  });

  const [onAddressSelect, setOnAddressSelect] = useState('');
  const navigate = useNavigate();

  // userInfo state onChange   
  const handleUserInfo = e => {
    const { name, value } = e.target; // e.target  name  value .
    setUserInfo({ ...userInfo, [name]: value }); // userInfo   name   value  .
  };

  // const isValidCheck =
  // userInfo?.name.length >= 2 && userInfo?.phone.length === 11; //     

  const handleAddressSelect = address => {
    setOnAddressSelect(address); //  API    data state 
    setUserInfo({
      // zipCode, address     useState 
      ...userInfo,
      zipCode: address.zonecode,
      address: address.address,
    });
  };

  const postSignUp = () => {
    //  API 
    // fetch('http://10.58.52.64:8000/users/kakao/address', {
    fetch(`${API.USERS}/kakao/address`, {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json;charset=utf-8',
        authorization: localStorage.getItem('accessToken'),
      },
      body: JSON.stringify({
        phoneNumber: userInfo.phone,
        zipCode: userInfo.zipCode,
        address: userInfo.address,
        addressDetails: userInfo.addressDetail,
      }),
    })
      .then(response => response.json())
      .then(result => {
        console.log(result);
        if (result.message === 'SUCCESS') {
          navigate('/main');
        } else {
          alert(' .');
        }
      });
  };

  const handleSubmitUserInfo = e => {
    e.preventDefault(); // submit   
    postSignUp();
  };

  const isPhoneNumberValid = userInfo.phone.length === 11;

  return (
    <Main id="main">
      <div>
        <SignUpContainer>
          <SignUpLeftSection>
            <span></span>
          </SignUpLeftSection>
          <SignUpRightSection>
            <SignUpForm
              onChange={handleUserInfo}
              onSubmit={handleSubmitUserInfo}
            >
              <fieldset>
                <SignUpLegend> </SignUpLegend>

                <SignUpInputWrap>
                  <SignUpAddressWrap>
                    <Input
                      borderRadius="4px"
                      value={userInfo.zipCode}
                      labelFlex="1"
                    />
                    <DaumPostCode onAddressSelect={handleAddressSelect} />
                  </SignUpAddressWrap>
                </SignUpInputWrap>
                <SignUpInputWrap>
                  <Input borderRadius="4px" value={userInfo.address} />
                </SignUpInputWrap>
                <SignUpInputWrap>
                  <Input
                    placeholder=" ."
                    borderRadius="4px"
                    name="addressDetail"
                  />
                </SignUpInputWrap>
                <SignUpInputWrap>
                  <Input
                    type="number"
                    placeholder=" ."
                    borderRadius="4px"
                    name="phone"
                  />
                </SignUpInputWrap>
                <SignUpButtonWrap>
                  <Button
                    type="submit"
                    disabled={!isPhoneNumberValid}
                    content=" "
                    shape="solid"
                    color="primary"
                    onClick={handleSubmitUserInfo}
                  />
                </SignUpButtonWrap>
              </fieldset>
            </SignUpForm>
          </SignUpRightSection>
        </SignUpContainer>
      </div>
    </Main>
  );
};

export default SnsSignUp;

const Main = styled.main`
  display: flex;
  align-items: center;
`;

const SignUpContainer = styled.div`
  display: flex;
  justify-content: center;
`;

const SignUpLeftSection = styled.section`
  flex: 1;
  border-radius: 4px;
  background: url(/images/account/bg_signup.jpg) no-repeat center/cover;
  font-size: 0;
`;

const SignUpRightSection = styled.section`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1;
`;

const SignUpForm = styled.form`
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 80px 20%;
`;

const SignUpLegend = styled.legend`
  font-size: 30px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 20px;
`;

const SignUpInputWrap = styled.div`
  padding: 15px 0;

  button {
    width: 150px;
  }

  input[type='number']::-webkit-outer-spin-button,
  input[type='number']::-webkit-inner-spin-button {
    -webkit-appearance: none;
    appearance: none;
    margin: 0;
  }
`;

const SignUpButtonWrap = styled.div`
  width: 100%;
  margin-top: 20px;
  color: ${props => props.theme.grayscaleF};
`;

const SignUpAddressWrap = styled.div`
  display: flex;

  gap: 8px;
`;

const SignUpEmailWrap = styled.div`
  display: flex;
  padding: 15px 0;
  gap: 8px;

  button {
    width: 100px;
  }
`;
