import React, { useState } from 'react';
import Router from './Router';
import { createStore } from 'redux';
import { Provider } from 'react-redux';
import { ThemeProvider } from 'styled-components';
import { lightTheme, darkTheme } from './styles/theme';
import ThemeSwitcher from './modules/themeSwitcher';
import GlobalStyle from './styles/GlobalStyle';

const Root = () => {
  // [Redux]    .       .
  let initialValue = [];

  // [Redux] reducer     (state)    (action)  .       .
  function reducer(state = initialValue, action) {
    //        .
    // console.log(action);
    // console.log(action.payload);

    // [Redux] initialValue (Immutability)  ,    .   copyValue . spread operator   .
    let copyValue = [...state];

    // [Redux] (action) , type . type     .
    if (action.type === 'ADD') {
      // [Redux] ADD   copyValue payload push. payload      .
      copyValue.push(action.payload);
      return copyValue;
    } else if (action.type === 'UPDATE') {
      copyValue.push(action.payload);
      return copyValue;
    }
  }

  // [Redux]     Redux     store .  store      .
  const store = createStore(reducer);

  // const isLogin = !!localStorage.getItem('accessToken');
  // const isKakao = !!localStorage.getItem('isKakao');
  // const isSeller = !!localStorage.getItem('isSeller');
  // const points = localStorage.getItem('points');
  // const cartCount = localStorage.getItem('cartCount');

  //    
  const isLogin = true;
  const isKakao = false;
  const isSeller = false;
  const points = 1000000;
  const cartCount = 0;

  const [isLightTheme, setIsLightTheme] = useState(true);
  const switchTheme = () => {
    setIsLightTheme(prev => !prev);
  };

  return (
    // [Redux]    Provider .
    <Provider store={store}>
      <ThemeProvider theme={isLightTheme ? lightTheme : darkTheme}>
        <GlobalStyle />
        <Router
          isLogin={isLogin}
          isKakao={isKakao}
          isSeller={isSeller}
          points={points}
          cartCount={cartCount}
        />
        {isLogin && (
          <ThemeSwitcher
            switchTheme={switchTheme}
            isLightTheme={isLightTheme}
          />
        )}
      </ThemeProvider>
    </Provider>
  );
};

export default Root;
