import React, { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { API } from '../../config';
import CheckBox from '../../components/CheckBox/CheckBox';
import Button from '../../components/Button/Button';
import CartCount from '../../components/CartCount/CartCount';
import styled from 'styled-components';

const Cart = () => {
  /**
   * @property {function} handleAllCheck =   /   
   * @property {function} checkedItemOriginalPrice =      
   * @property {function} checkedItemDiscountedAmount =       
   * @property {function} checkedItemTotalPrice =      
   * @property {function} handleItemPrice =    cartData data  checkItem  
   * @property {function} handleItemIdInfoChange =     productId data  checkItem   (    )
   * @property {function} handleMarketCheck =    /   
   * @property {function} getCartInfoData =     GET 
   * @property {function} patchCheckItemBtn =      PATCH 
   * @property {function} patchItemInfo =   productId quantity  checkItem  
   * @property {function} deleteCheckItemBtn =      DELETE 
   * @property {function} handleQuantityChange =       
   */

  // hook
  const [cartData, setCartData] = useState([]);
  const [selectAllChecked, setSelectAllChecked] = useState(false); // 1.       State 
  const [checkItem, setCheckItem] = useState([]);
  const navigate = useNavigate();

  // function
  const getMokData = () => {
    fetch('/data/CartData.json')
      .then(Response => Response.json())
      .then(result => setCartData(result.data));
  };

  const cartListData =
    cartData !== undefined &&
    cartData
      .map(item => {
        return item.products?.map(product => {
          return product.productId;
        });
      })
      .flat();

  //  

  const handleAllCheck = checked => {
    // 1.      State 
    setSelectAllChecked(!selectAllChecked); // 2.     

    setCheckItem(selectAllChecked ? [] : handleItemPrice()); // 3.      cartData checkItem ,    checkItem   
  };

  const checkedItemOriginalPrice = (checked, productId) => {
    //     
    const itemPrice = checkItem?.map(item => {
      return item.totalPrice;
    });

    return itemPrice;
  };

  const checkedItemDiscountedAmount = (checked, productId) => {
    //      
    const itemPrice = checkItem
      ?.map(item => {
        return item.discountedAmount;
      })
      .flat();
    return itemPrice;
  };

  const checkedItemTotalPrice = (checked, productId) => {
    //         
    const itemPrice = checkItem
      ?.map(item => {
        return item.totalPrice - (item.discountedAmount || 0);
      })
      .flat();
    return itemPrice;
  };

  const ItemOriginalPrice = checkedItemOriginalPrice(); //       
  const ItemTotalPrice = checkedItemTotalPrice(); //        
  const ItemDiscountAmount = checkedItemDiscountedAmount(); //        

  const totalOriginalPrice = ItemOriginalPrice.reduce(
    //    reduce  
    (acc, cur) => acc + cur,
    0,
  );
  const totalDiscountAmount = ItemDiscountAmount.reduce(
    //     reduce  
    (acc, cur) => acc + cur,
    0,
  );
  const totalPrice = ItemTotalPrice.reduce((acc, cur) => acc + cur, 0); //     reduce  

  const handleItemPrice = () => {
    //    cartData data  checkItem  
    const itemPrice = cartData
      ?.map(item => {
        return item.products.map(product => ({
          sellerId: item.sellerId,
          productId: product.productId,
          quantity: product.quantity,
          originalPrice: product.originalPrice,
          discountedAmount: product.discountedAmount,
          totalPrice: product.originalPrice * product.quantity,
        }));
      })
      .flat(); // flat() :    (2  1  )
    return itemPrice;
  };

  const handleItemIdInfoChange = () => {
    //     productId data  checkItem   (    )
    const itemIdInfo = checkItem
      .map(item => ({
        productId: item.productId,
      }))
      .flat(); // flat() :    (2  1  )
    return itemIdInfo;
  };

  const handleMarketCheck = (checked, sellerId) => {
    //    /   
    if (checked) {
      const unMarketData = checkItem.filter(item => item.sellerId !== sellerId); // sellerId   checkItem unMarketData 

      const marketData = cartData.filter(cart => cart.sellerId === sellerId); // sellerId  cartData marketData 

      marketData.map(item => {
        return item.products.map(product => {
          return unMarketData.push({
            sellerId: item.sellerId,
            productId: product.productId,
            quantity: product.quantity,
            originalPrice: product.originalPrice,
            discountedAmount: product.discountedAmount,
            totalPrice: product.originalPrice * product.quantity,
          });
        });
      });

      setCheckItem(unMarketData);
    } else {
      //     sellerCheckedItem sellerId 

      const unCheckMarketData = checkItem.filter(
        //
        cart => cart.sellerId !== sellerId,
      );

      setCheckItem(unCheckMarketData);
    }
  };

  //     GET 
  // const FETCH_URL = 'http://10.58.52.176:8000/carts';

  const getCartInfoData = () => {
    // fetch(FETCH_URL, {
    fetch(`${API.CART}`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        authorization: localStorage.getItem('accessToken'),
      },
    })
      .then(response => response.json())
      .then(result => {
        setCartData(result.data);
      });
  };

  //      PATCH 
  const patchCheckItemBtn = () => {
    // fetch(FETCH_URL, {
    fetch(`${API.CART}`, {
      method: 'PATCH',
      headers: {
        'Content-Type': 'application/json',
        authorization: localStorage.getItem('accessToken'),
      },
      body: JSON.stringify({ data: patchItemInfo() }),
    })
      .then(response => response.json())
      .then(result => {
        navigate(`/order`, {
          state: {
            cartPriceData: {
              totalOriginalPrice,
              totalDiscountAmount,
              totalPrice,
            },
          },
        });
      });
  };

  const patchItemInfo = () => {
    //   productId quantity  checkItem  
    const itemInfo = checkItem
      ?.map(item => {
        return {
          productId: item.productId,
          quantity: item.quantity,
        };
      })

      .flat();
    return itemInfo;
  };

  const deleteCheckItemBtn = () => {
    //      DELETE 
    // fetch(FETCH_URL, {
    fetch(`${API.CART}`, {
      method: 'DELETE',
      headers: {
        'Content-Type': 'application/json',
        authorization: localStorage.getItem('accessToken'),
      },
      body: JSON.stringify({ data: handleItemIdInfoChange() }),
    })
      .then(response => response.json())
      .then(result => {
        if (result.message === 'Delete item!') {
          window.location.reload();
        }
      });
  };

  const handleQuantityChange = (productId, newQuantity) => {
    //       
    // cartCount Component  productId newQuantity  
    // fetch(FETCH_URL + '/updatequantity', {
    fetch(`${API.CART}/updatequantity`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        authorization: localStorage.getItem('accessToken'),
      },
      body: JSON.stringify({
        data: { productId, quantity: Number(newQuantity) },
      }), // productId         POST 
    })
      .then(response => response.json())
      .then(result => {
        if (result.message === 'Update Success!') {
          alert(' .');
          getCartInfoData();
        } else {
          alert('  .');
        }
      }); //       .
  };

  // useEffect
  //      useEffect
  useEffect(() => {
    getMokData();
    // getCartInfoData();
  }, []);

  return (
    <Main id="main">
      <div>
        <CartTitle></CartTitle>
        <CartContainer>
          <CartLeftSection>
            <CartLeftWrap>
              <CheckBox
                size="small"
                onChange={e => handleAllCheck(e.target.checked)}
                checked={checkItem.length === (cartListData.length || 0)}
              />
              <CartAllCheckText></CartAllCheckText>
              <CartSelectDeleteBtn onClick={deleteCheckItemBtn}>
                
              </CartSelectDeleteBtn>
            </CartLeftWrap>
            {!!cartData === false && (
              <CartIsEmpty>   .</CartIsEmpty>
            )}
            {!!cartData &&
              cartData?.map((item, index) => {
                return (
                  <>
                    <CartMarketTitleWrap>
                      <CartMarketItemWrap>
                        <CheckBox
                          size="small"
                          onChange={e =>
                            handleMarketCheck(e.target.checked, item.sellerId)
                          }
                          checked={
                            !!checkItem.find(checked => {
                              //checked.sellerId === item.sellerId;
                              return checked.sellerId === item.sellerId;
                            })
                          }
                        />
                        <h3>{item.sellerName}</h3>
                      </CartMarketItemWrap>
                    </CartMarketTitleWrap>
                    {item.products?.map((item, index) => {
                      const handleSingleCheck = (checked, productId) => {
                        //     
                        if (checked) {
                          //    checkItem productId quantity 
                          return setCheckItem([
                            ...checkItem,
                            {
                              sellerId: item.sellerId,
                              productId: item.productId,
                              quantity: item.quantity,
                              originalPrice: item.originalPrice,
                              discountedAmount: item.discountedAmount,
                              totalPrice: item.originalPrice * item.quantity,
                            },
                          ]);
                        } else {
                          //     checkItem productId quantity 
                          return setCheckItem(
                            checkItem.filter(
                              item => item.productId !== productId,
                            ),
                          );
                        }
                      };
                      return (
                        <CartItemBoxWrap key={index}>
                          <CartItemCheckBoxWrap>
                            <CheckBox
                              size="small"
                              onChange={e =>
                                handleSingleCheck(
                                  e.target.checked,
                                  item.productId,
                                )
                              }
                              checked={
                                // !! :  
                                !!checkItem.find(
                                  // find()  checkItem productId item productId     
                                  checked =>
                                    checked.productId === item.productId,
                                )
                              }
                            />
                          </CartItemCheckBoxWrap>
                          <CartItemUl>
                            <CartItemLi>
                              <CartItemImgWrap>
                                <img src={item.productImage} alt="itemImage" />
                              </CartItemImgWrap>
                            </CartItemLi>
                            <CartItemLi>
                              <h3>{item.productName}</h3>
                            </CartItemLi>
                            <CartItemLi>
                              <CartItemCounterWrap>
                                <CartCount
                                  productId={item.productId}
                                  quantity={item.quantity}
                                  onQuantityChange={handleQuantityChange}
                                />
                              </CartItemCounterWrap>
                            </CartItemLi>
                            <CartItemLi>
                              <h3>
                                {item.totalPrice
                                  .toString()
                                  .replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                                
                              </h3>
                            </CartItemLi>
                            <CartItemLi>
                              <span> </span>
                            </CartItemLi>
                          </CartItemUl>
                        </CartItemBoxWrap>
                      );
                    })}
                  </>
                );
              })}
          </CartLeftSection>
          <CartRightSection>
            <CartReceiptContainer>
              <CartTable>
                <CartHeader>
                  <th>
                    <CartReceiptTitle> </CartReceiptTitle>
                  </th>
                </CartHeader>
                <tbody>
                  <CartTr>
                    <th>
                      <CartReceiptText> </CartReceiptText>
                    </th>
                    <td>
                      <CartReceiptText>
                        {totalOriginalPrice
                          .toString()
                          .replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                        
                      </CartReceiptText>
                    </td>
                  </CartTr>

                  <CartDisCountTr>
                    <th>
                      <CartReceiptText> </CartReceiptText>
                    </th>
                    <td>
                      <CartReceiptPrice>
                        {totalDiscountAmount
                          .toString()
                          .replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                        
                      </CartReceiptPrice>
                    </td>
                  </CartDisCountTr>
                  <CartTr>
                    <th>
                      <CartReceiptSmallText>  </CartReceiptSmallText>
                    </th>
                    <td>
                      <CartReceiptSmallPrice>
                        {totalDiscountAmount
                          .toString()
                          .replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                        
                      </CartReceiptSmallPrice>
                    </td>
                  </CartTr>
                  <CartTr>
                    <th>
                      <CartReceiptText> </CartReceiptText>
                    </th>
                    <td>
                      <CartReceiptText>0</CartReceiptText>
                    </td>
                  </CartTr>
                  <CartTr>
                    <th>
                      <CartReceiptText>  </CartReceiptText>
                    </th>
                    <td>
                      <CartReceiptText>
                        {totalPrice
                          .toString()
                          .replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                        
                      </CartReceiptText>
                    </td>
                  </CartTr>
                  <CartLastTr>
                    <td>
                      <Button
                        shape="solid"
                        size="large"
                        color="primary"
                        content={`   : ${totalPrice
                          .toString()
                          .replace(/\B(?=(\d{3})+(?!\d))/g, ',')}`}
                        onClick={patchCheckItemBtn}
                      />
                      {/* <Button onClick={MarketDataInfo} /> */}
                    </td>
                  </CartLastTr>
                </tbody>
              </CartTable>
            </CartReceiptContainer>
          </CartRightSection>
        </CartContainer>
      </div>
    </Main>
  );
};

export default Cart;

