import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { API } from '../../config';
import styled from 'styled-components';
import Input from '../../components/Input/Input';
import Button from '../../components/Button/Button';

/**
 * Login.js logics
 * @property {function} handleLogin =        (   RedirectUri )
 * @property {function} handleUserInfo = userInfo state onChange   
 * @property {function} postUserInfo =     POST  
 * @property {function} handleLoginSubmit =      
 */

const Login = () => {
  const [userInfo, setUserInfo] = useState({
    //  Valid  state
    email: '',
    password: '',
  });
  const navigate = useNavigate();

  //     
  const RestApiKey = process.env.REACT_APP_RestApiKey;
  const RedirectUri = process.env.REACT_APP_RedirectUri;
  const kakaoURL = `https://kauth.kakao.com/oauth/authorize?client_id=${RestApiKey}&redirect_uri=${RedirectUri}&response_type=code`;

  // userInfo Email, Password Valid Check
  const emailRegExp =
    /^[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*\.[a-zA-Z]{2,3}$/i; //  
  const passwordRegExp = /^(?=.*[a-zA-Z])(?=.*[!@#$%^*+=-])(?=.*[0-9]).{8,20}$/; //   

  //   
  const isEmailValid = emailRegExp.test(userInfo.email); //   
  const isPasswordValid = passwordRegExp.test(userInfo.password); //   

  //     
  const isValidCheck = isEmailValid && isPasswordValid;

  const handleLogin = () => {
    window.location.href = kakaoURL;
  };

  const handleUserInfo = e => {
    const { name, value } = e.target;
    setUserInfo({ ...userInfo, [name]: value });
  };

  const postUserInfo = () => {
    // fetch('http://10.58.52.126:8000/users/', {
    fetch(`${API.USERS}`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json;charset=utf-8',
      },
      body: JSON.stringify({
        email: userInfo.email,
        password: userInfo.password,
      }),
    })
      .then(response => response.json())
      .then(result => {
        console.log(result);
        if (result.message === 'SUCCESS') {
          localStorage.setItem('accessToken', result.accessToken.accessToken);
          localStorage.setItem('isKakao', result.accessToken.isKakao);
          localStorage.setItem('isSeller', result.accessToken.isSeller);
          localStorage.setItem('points', result.accessToken.points);
          localStorage.setItem('cartCount', result.accessToken.cartCount);
          navigate('/main');
          window.location.reload();
        } else {
          alert(' .  .');
        }
      });
  };

  const handleLoginSubmit = e => {
    e.preventDefault();
    postUserInfo();
  };

  return (
    <Main id="main">
      <div>
        <LoginContainer>
          <LoginLeftSection></LoginLeftSection>
          <LoginRightSection>
            <LoginForm onChange={handleUserInfo} onSubmit={handleLoginSubmit}>
              <fieldset>
                <LoginLegend></LoginLegend>
                <LoginInputWrap>
                  <Input
                    type="text"
                    name="email"
                    placeholder=""
                    borderRadius="4px"
                    status={
                      (userInfo.email === '' && 'default') ||
                      (!isEmailValid &&
                        userInfo.email.length >= 1 &&
                        'error') ||
                      (isEmailValid && 'done')
                    }
                    error="   ."
                  />
                </LoginInputWrap>
                <LoginInputWrap>
                  <Input
                    type="password"
                    name="password"
                    placeholder=""
                    borderRadius="4px"
                    status={
                      (userInfo.password === '' && 'default') ||
                      (!isPasswordValid &&
                        userInfo.password.length >= 1 &&
                        'error') ||
                      (isPasswordValid && 'done')
                    }
                    error=" 8~20  , ,  ."
                  />
                </LoginInputWrap>
                <LoginButtonWrap>
                  <Button
                    type="submit"
                    content=""
                    shape="solid"
                    color="primary"
                    disabled={!isValidCheck}
                    onClick={handleLoginSubmit}
                  />
                </LoginButtonWrap>
                <LoginSnsButtonWrap>
                  <button type="button" onClick={handleLogin}>
                    <img
                      src="/images/kakao/kakao_login_medium_wide.png"
                      alt="kakao"
                    />
                  </button>
                </LoginSnsButtonWrap>
              </fieldset>
            </LoginForm>
          </LoginRightSection>
        </LoginContainer>
      </div>
    </Main>
  );
};

export default Login;

const Main = styled.main`
  display: flex;
  align-items: center;
`;

const LoginContainer = styled.div`
  display: flex;
  justify-content: center;
`;

const LoginLeftSection = styled.section`
  flex: 1;
  border-radius: 4px;
  background: url(/images/account/bg_login.jpg) no-repeat center/cover;
  font-size: 0;
`;

const LoginRightSection = styled.section`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1;
`;

const LoginForm = styled.form`
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 40px 20%;
`;

const LoginLegend = styled.legend`
  font-size: 30px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 20px;
`;

const LoginInputWrap = styled.div`
  padding: 15px 0;
`;

const LoginButtonWrap = styled.div`
  margin-top: 20px;
`;

const LoginSnsButtonWrap = styled.div`
  position: relative;
  width: 100%;
  padding-top: 80px;

  button {
    width: 100%;
    border-radius: 4px;
    background: #fee500; //      
    font-size: 0;

    img {
      width: auto;
      height: 100%;
    }
  }

  &::before {
    content: '';
    position: absolute;
    top: 40px;
    left: 50%;
    transform: translateX(-50%);
    display: block;
    width: 50%;
    height: 1px;
    margin: 0 auto;
    background-color: ${props => props.theme.grayscaleE};
  }
`;
