import React from 'react';
import { useDaumPostcodePopup } from 'react-daum-postcode';
import Button from '../../../components/Button/Button';

/**
 * @property {function} open =     
 * @property {function} handelComplete =      
 * @property {function} handelClick =     
 */

const DaumPostCode = props => {
  const open = useDaumPostcodePopup(
    '//t1.daumcdn.net/mapjsapi/bundle/postcode/prod/postcode.v2.js',
  ); //     

  const handelComplete = data => {
    let fullAddress = data.address; //   
    let extraAddress = ''; //   

    if (data.addressType === 'R') {
      //     
      if (data.bname !== '') {
        //   
        extraAddress += data.bname; // , 
      }
      if (data.buildngName !== '') {
        //   
        extraAddress +=
          extraAddress !== '' ? `, ${data.buildngName}` : data.buildngName; // 
      }
      //      
      fullAddress += extraAddress !== '' ? ` (${extraAddress})` : '';
    }
    props.onAddressSelect(data); // API    data props   
  };

  const handelClick = () => {
    //      (   OPEN)
    open({
      onComplete: handelComplete,
    });
  };

  return (
    <Button
      onClick={handelClick} //  API 
      content=" "
      shape="solid"
      color="primary"
    />
  );
};

export default DaumPostCode;
