import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import Portal from '../../../Modal/Portal';
import Modal from '../../../Modal/Modal';
import Category from '../../../Modal/Contents/Category';
import styled from 'styled-components';

const NavListItem = ({ id, text, path }) => {
  // 5. Modal    useState hook .  false.
  const [modalOpen, setModalOpen] = useState(false);

  // 6. Modal    .
  const modalHandler = () => {
    setModalOpen(prev => !prev);
  };

  useEffect(() => {
    // 9. ESC  Modal      .
    const close = e => {
      if (e.keyCode === 27) {
        setModalOpen(false);
      }
    };

    // 10. keydown  Modal    .
    window.addEventListener('keydown', close);
  }, []);

  const isCategory = id === 2;

  return (
    <ListItem>
      {!isCategory && <Link to={path}>{text}</Link>}
      {isCategory && (
        // 7. Modal      .
        // 8. 41  Portal     modalOpen true Modal  .
        <button type="button" onClick={modalHandler}>
          {text}
        </button>
      )}
      <Portal>
        {modalOpen && (
          <Modal
            data={<Category onClose={modalHandler} />}
            scale="large"
            onClose={modalHandler}
          />
        )}
      </Portal>
    </ListItem>
  );
};

const ListItem = styled.li`
  position: relative;
  padding: 20px 0;
  cursor: pointer;

  a,
  button {
    position: relative;
    font-size: 24px;
    color: ${props => props.theme.grayscaleF};

    &::after {
      content: '';
      width: 8px;
      height: 8px;
      position: absolute;
      top: -6px;
      right: -6px;
      border-radius: 50%;
      background-color: ${props => props.theme.primaryColor};
      opacity: 0;
      transition: opacity 0.2s ease-out;
    }

    &:hover,
    &:focus {
      &::after {
        opacity: 1;
      }
    }
  }
`;

export default NavListItem;
